<template>
  <div class="changeCount">
    <button @click="sub" class="min">-</button>
    <input :value="value" @change="ret" class="inp" type="text">
    <button @click="add" class="more">+</button>
  </div>
</template>

<script>
export default {
  name: 'CountBox',
  props: {
    value: {
      type: Number,
      default: 1
    }
  },
  methods: {
    sub () {
      if (this.value <= 1) return
      this.$emit('input', this.value - 1)
    },
    add () {
      this.$emit('input', this.value + 1)
    },
    ret (e) {
      const num = +e.target.value
      if (isNaN(num) || num < 1) {
        e.target.value = this.value
      }
    }
  }
}
</script>

<style lang="less" scoped>
.changeCount {
  width: 110px;
  height: 20px;
  display: flex;
  .min, .more{
    width: 20px;
    height: 20px;
  }
  .inp {
    margin: 0 5px;
    width: 25px;
    height: 20px;
    text-align: center;
  }
}
</style>
