import { getCartList, changeCount, delSelect } from '@/api/cart'
export default {
  namespaced: true,
  state () {
    return {
      cartList: []
    }
  },
  mutations: {
    // 存储购物车数组
    setCartList (state, newList) {
      state.cartList = newList
    },
    // 购物车单选
    changeSel (state, goodsId) {
      const res = state.cartList.find(item => item.goods_id === goodsId)
      res.isChecked = !res.isChecked
      // console.log(res)
    },
    // 购物车全选、反选
    changeAllSel (state, value) {
      state.cartList.forEach(item => { item.isChecked = value })
    },
    // 修改商品数量(vuex)
    changeCount (state, { value, goodsId }) {
      const goods = state.cartList.find(item => item.goods_id === goodsId)
      goods.goods_num = value
    }
  },
  actions: {
    // 获取购物车商品
    async getCartAction (context) {
      const { data } = await getCartList()
      data.list.forEach(item => { item.isChecked = false })
      context.commit('setCartList', data.list)
    },
    // 修改商品数量(+后台)
    async changeCountAction (context, { value, goodsId, skuId }) {
      context.commit('changeCount', { value, goodsId })
      await changeCount(goodsId, value, skuId)
    },
    // 删除购物车商品
    async handelDelSelect (context) {
      const selCartList = context.getters.selCartList
      const cartIds = selCartList.map(item => item.id)
      await delSelect(cartIds)

      // 重新拉取最新的购物车数据 (重新渲染)
      context.dispatch('getCartAction')
    }
  },
  getters: {
    // 全选
    isAllChecked (state) {
      return state.cartList.every(item => item.isChecked)
    },
    // 购物车总数
    cartTotal (state) {
      return state.cartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 购物车 选中商品
    selCartList (state) {
      return state.cartList.filter(item => item.isChecked)
    },
    // 购物车 选中商品总数
    selCount (state, getters) {
      return getters.selCartList.reduce((sum, item) => sum + item.goods_num, 0)
    },
    // 购物车 选中价格总数
    selPrice (state, getters) {
      return getters.selCartList.reduce((sum, item) => sum + item.goods_num * item.goods.goods_price_min, 0).toFixed(2)
    }
  }
}
